.checkboxInput {
    border-radius: 4px;
    cursor: pointer;
    border: 2px solid #522987 !important;
    accent-color: #522987;
    width: 100px;
    height: 100px;
    margin-top: 2.5%;
    &:indeterminate {
        background-color: #ba86ff; // color when checkbox is in indeterminate state
        border-color: #9f88bc;
    }
}


.dropdownCheckbox {
    @extend .checkboxInput;
    margin-top: 0;
    margin-right: 8px;
}